.s-solutions {
  margin-top: rem(150);
  padding-bottom: rem(155);
  background-image: url(/img/solutions/back.png);
  background-position: right;
  background-repeat: no-repeat;
  background-size: revert;
  @media (max-width: 1600px) {
    background-image: url(/img/solutions/back_tablet.png);
  }

  @media (max-width: 991px) {
    background-image: url(/img/solutions/back.png);
    background-position: right bottom;
    // background-position: 130%;
  }

  @media (max-width: 768px) {
    background-size: rem(390) rem(615);
  }

  @media (max-width: 500px) {
    background-size: rem(260) rem(420);
  }

  .l-default {
    max-width: rem(1520);
  }

  &__title {
    margin-bottom: rem(45);
  }

  &__text {
    max-width: 80%;
    margin-bottom: rem(30);
    font-size: rem(19);
    font-weight: 400;
    line-height: 170%;
    color: $grey_light_more_2;
    @media (max-width: 1600px) {
      max-width: 75%;
    }

    @media (max-width: 1040px) {
      font-size: rem(17);
    }

    @media (max-width: 768px) {
      max-width: 100%;
      margin-bottom: rem(15);
      font-size: rem(15);
    }

    @media (max-width: 768px) {
      font-size: rem(13);
    }

    &--last {
      margin-bottom: 0;
      max-width: 100%;

      @media (min-width: 768px) {
        max-width: 70%;
      }
    }

    &--short {
      margin-top: rem(96);
      margin-left: rem(100);

      @media (max-width: 1040px) {
        margin-top: rem(58);
        margin-left: rem(70);
      }

      @media (max-width: 1040px) {
        margin-left: rem(40);
      }
    }
  }
}
