.s-top-categories {
    padding: rem(14) 0;
    background: rgba(0, 0, 0, 0.4);
    margin-top: rem(55);

    @media (min-width: 768px) {
        padding: rem(18) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(22) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(28) 0;
    }

    @media (min-width: 1920px) {
        padding: rem(15) 0;
    }

    &__title {
        margin-bottom: rem(60);
    }

    &__wrapper {
        width: rem(1040);
        margin: 0 auto;
    }

    &__item {
        position: relative;
        height: rem(103);
        overflow: hidden;
        cursor: pointer;
        border-radius: rem(5);

        @media (min-width: 768px) {
            height: rem(136);
        }

        @media (min-width: 1024px) {
            height: rem(161);
        }

        @media (min-width: 1600px) {
            height: rem(187);
        }

        // &:after {
        //     content: "";
        //     display: block;
        //     position: absolute;
        //     top: -2.5%;
        //     left: -2.5%;
        //     width: 105%;
        //     height: 105%;
        //     transition: all 0.5s;
        //     opacity: 0.5;
        // }

        &-img {
            width: 100%;
            height: 100%;
            overflow: hidden;

            img {
                border-radius: rem(5);
            }
        }

        &-title {
            position: absolute;
            z-index: 2;
            bottom: rem(30);
            left: rem(15);
            font-size: rem(19);
            font-weight: 700;
            line-height: 1.2;
            color: $grey_light_more_2;
        }
    }

    &__slide {
        width: rem(146);
        height: auto;
        transition: 0.5s;

        @media (min-width: 768px) {
            width: rem(193);
        }

        @media (min-width: 1024px) {
            width: rem(228);
        }

        @media (min-width: 1600px) {
            width: rem(265);
        }

        &:hover {
            box-shadow: 0 0 rem(35) rgba(168, 168, 174, 0.5);
        }

        &.swiper-slide-active {
            box-shadow: 0 0 rem(35) rgba(168, 168, 174, 0.5);
        }
    }
}