.s-advertising {
    position: relative;
    background: url(/img/back/back.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    .l-default {
        max-width: rem(1720);
        padding: 0 rem(65);
        @media (max-width: 500px) {
            padding: 0 !important; 
        }
    }

    &__title {
        padding: rem(176) 0 rem(90);
        font-family: "Roboto";
        font-size: rem(120);
        font-weight: 600;
        line-height: 98%;
        background: linear-gradient(46.06deg, #18191f 14.09%, #262b38 78.25%);
        letter-spacing: -0.03em;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;

        @media (max-width: 1600px) {
            font-size: rem(110);
            line-height: 98%;
        }

        @media (max-width: 1024px) {
            padding: rem(83) 0 rem(100);
            font-size: rem(70);
        }

        @media (max-width: 768px) {
            padding: rem(49) 0 rem(94);
            font-size: rem(50);
        }

        @media (max-width: 500px) {
            padding: rem(317) 0 rem(109);
            font-size: rem(38);
        }
    }

    &::after {
        content: "эталон";
        position: absolute;
        right: -1%;
        bottom: -42%;
        font-size: rem(400);
        font-weight: 500;
        line-height: 110%;
        color: transparent;
        letter-spacing: -0.03em;
        -webkit-text-stroke: rem(3) #ffffff3f;
        text-stroke: rem(3) #ffffff3f;

        @media (max-width: 1600px) {
            right: -7%;
        }

        @media (max-width: 1024px) {
            right: -1%;
            bottom: -33%;
            font-size: rem(250);
        }

        @media (max-width: 768px) {
            right: -10%;
            bottom: -45%;
            font-size: rem(200);
        }

        @media (max-width: 500px) {
            bottom: -13%;
            font-size: rem(120);
        }
    }
}
