.s-rulers {
  border-bottom: rem(1) solid $red;
  margin-top: rem(30);

  .l-default {
    max-width: rem(1520);
  }

  &__tabs {
    margin: 0 0 rem(-1) 0;
    display: flex;
    border-bottom: rem(1) solid $red;
  }

  &__tab {
    font-weight: 400;
    font-size: rem(13);
    line-height: 1.2;

    color: $grey;
    transition: 0.4s;
    box-sizing: border-box;

    margin: 0 0 rem(-1) 0;
      @media (min-width: 768px) {
        font-size: rem(15);

      }
      @media (min-width: 1024px) {
        font-size: rem(19);
      }

      @media (min-width: 1600px) {
        font-size: rem(25);
      }

      @media (min-width: 1920px) {
        font-size: rem(21);
      }
    
    &-title{
      border-right: rem(2) solid $grey;
      padding: 0 rem(20);
      margin: rem(12) 0;
      transition: 0.4s;
      span{
        display: none;
        @media (min-width: 768px) {
          display: inline-block;
        }
      }
    }
    &:last-child {
      border-right: none;
    }
    :hover{
      color: $grey_light_more_2;
    }

    &.is-active {
      border: rem(1) solid $red;
      border-bottom: rem(1) solid $black;
      color: $grey_light_more_2;
      margin-left: rem(-2);
      .s-rulers__tab-title{
        padding: 0 rem(40);
        border: none;
        display: flex;
          span{
            display: inline-block;
            margin-right: 5px;
        }
      }
    }
    &-content{
      display: none;
    }
  }
  .border-none{
    border: none;
    .s-rulers__tab-link{
        // border: none;
    }
    .s-rulers__tab-title{
        border: none;
    }
  }
  
}
.is-actived{
  display: block !important;
}