.s-goods {
    margin-top: rem(136);

    .l-default {
        max-width: rem(1520);
    }

    .number__two {
        display: block;
    }

    &__title {
        margin-bottom: rem(15);
        font-size: rem(19);
    }

    &__text {
        margin-bottom: rem(25);

        @media (min-width: 768px) {
            margin-bottom: rem(80);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(60);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(130);
        }

        &--prefix {
            font-size: rem(14);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                font-size: rem(15);

                &:first-child {
                    margin-bottom: rem(15);
                }
            }

            @media (min-width: 1024px) {
                font-size: rem(17);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
            }

            &:first-child {
                margin-bottom: rem(10);
            }
        }
    }
    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @media (min-width: 768px) {
            flex-wrap: nowrap;
        }

        &--item {
            max-width: rem(166);
            padding: rem(23);
            border: rem(1) solid transparent;
            border-radius: rem(5);
            -moz-border-image: -moz-linear-gradient(
                rgba(132, 132, 132, 1),
                rgba(195, 195, 195, 1),
                rgba(37, 37, 37, 1)
            );
            -webkit-border-image: -webkit-linear-gradient(
                rgba(132, 132, 132, 1),
                rgba(195, 195, 195, 1),
                rgba(37, 37, 37, 1)
            );
            border-image: linear-gradient(
                rgba(132, 132, 132, 1),
                rgba(195, 195, 195, 1),
                rgba(37, 37, 37, 1)
            );

            border-image-slice: 1;

            @media (min-width: 768px) {
                max-width: rem(216);
                padding: rem(28);

                &:nth-child(2) {
                    margin: 0 rem(15);
                }
            }

            @media (min-width: 1024px) {
                max-width: rem(283);
                padding: rem(37);
            }

            @media (min-width: 1600px) {
                max-width: rem(467);
                padding: rem(60);
            }

            &:last-child {
                position: relative;
                max-width: 100%;
                margin: rem(35) auto 0;

                @media (min-width: 540px) {
                    max-width: rem(166);
                    margin: 0;
                    .s-goods__item--img,
                    .s-goods__item--name,
                    .s-goods__item--subtitle,
                    .s-goods__item--specifications,
                    .s-goods__item--more,
                    .s-goods__item--buy {
                        display: block;
                        opacity: 0.5;
                    }
                }

                @media (min-width: 768px) {
                    max-width: rem(216);
                }

                @media (min-width: 1024px) {
                    max-width: rem(283);
                }

                @media (min-width: 1600px) {
                    max-width: rem(467);
                }

                .s-goods__item--img,
                .s-goods__item--name,
                .s-goods__item--subtitle,
                .s-goods__item--specifications,
                .s-goods__item--more,
                .s-goods__item--buy {
                    display: none;
                }

                &::after {
                    // position: absolute;
                    position: relative;
                    top: 0;
                    left: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 100%;
                    // opacity: 1;
                    margin: 0 auto;
                    padding-top: 0;
                    font-size: rem(13);
                    font-weight: 600;
                    line-height: 1.2;
                    text-align: center;
                    color: $white;
                    cursor: pointer;
                    border-radius: rem(5);
                    content: "Следующая линейка";
                    box-sizing: border-box;
                    background-color: rgba(0, 0, 0, 0.722);

                    @media (min-width: 540px) {
                        position: absolute;
                        padding-top: rem(60);
                    }

                    @media (min-width: 768px) {
                        font-size: rem(15);
                    }

                    @media (min-width: 1024px) {
                        font-size: rem(21);
                        // top: 50%;
                    }

                    @media (min-width: 1600px) {
                        font-size: rem(30);
                    }
                    @media (min-width: 1920px) {
                        font-size: rem(35);
                    }
                }
            }
        }
    }

    &__item {
        &--img {
            max-width: rem(123);
            margin: 0 0 rem(12) 0;
            box-sizing: border-box;

            @media (min-width: 768px) {
                max-width: rem(154);
                margin-bottom: rem(15);
            }

            @media (min-width: 1024px) {
                max-width: rem(200);
                margin-bottom: rem(16);
            }

            @media (min-width: 1600px) {
                max-width: rem(340);
                margin-bottom: rem(30);
            }

            img {
                width: 100%;
                margin: 0;
            }
        }

        &--text {
            font-size: rem(10);

            @media (min-width: 1024px) {
                font-size: rem(13);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
            }
        }

        &--name {
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                margin-bottom: rem(6);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(10);
            }

            @media (min-width: 1600px) {
                font-size: rem(21);
            }

            &:last-child {
                display: none;
            }
        }

        &--subtitle {
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light;
            @media (min-width: 768px) {
                margin-bottom: rem(6);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(10);
            }
        }

        &--specifications {
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                margin-bottom: rem(20);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(30);
            }
        }
        &--more,
        &--buy {
            display: block;
            margin-bottom: rem(5);
            padding: rem(5) 0;
            text-align: center;
            color: $grey_light_more_2;
            border: rem(1) solid $grey_white;
            border-radius: rem(7);

            @media (min-width: 768px) {
                padding: rem(8) 0;
            }

            @media (min-width: 1024px) {
                padding: rem(12) 0;
            }
        }

        &--buy {
            margin-bottom: 0;
        }
    }
}
